<template>
  <section class="expenses-season">
    <!-- title -->
    <div class="text-center mb-2">
      <div class="d-flex align-items-center justify-content-center mb-2">
        <b-avatar class="mr-1" size="40" variant="light-danger">
          <img :src="require('@/assets/icons/building_icon.svg')" alt="" draggable="false" width="30" />
        </b-avatar>
        <h4 class="m-0">سرفصل هزینه ها</h4>
      </div>
      <span> لورم ایپسوم متن ساختگی با تولید سادگی نامفهوم از صنعت چاپ و با استفاده از طراحان گرافیک است12 </span>
    </div>

    <!-- main -->
    <main>
      <b-card class="card-one">
        <div class="d-flex justify-content-start align-items-center">
          <!-- icon -->
          <b-avatar class="mr-1" size="36" style="background: #fceaeb">
            <chart-icon :style="{ color: 'var(--danger)' }" />
          </b-avatar>

          <!-- title -->
          <h4 class="title">سرفصل های موجود</h4>
        </div>
        <div class="mt-1 pt-1">
          <h4 v-if="getExpenses.length == 0" class="title">هیچ سرفصلی وجود ندارد</h4>
          <ul class="d-flex flex-column px-0 expenses-list">
            <li v-for="(item, key) in getExpenses" :key="key" class="pb-1">
              <div class="d-flex justify-content-between justify-space-between ln">
                <span class="pointer" @click="fill(item, key)">{{ item }}</span>
                <span><feather-icon class="pointer" style="margin-left: 5px; opacity: 0.7" icon="Trash2Icon" size="16" @click="remove(key, item)" /></span>
              </div>
            </li>
          </ul>
        </div>
      </b-card>

      <b-card class="card-two">
        <div class="d-flex justify-content-start align-items-center">
          <!-- icon -->
          <b-avatar class="mr-1" size="36" variant="light-info">
            <chart-icon :style="{ color: 'var(--info)' }" />
          </b-avatar>

          <!-- title -->
          <h4 class="title">ثبت عنوان دلخواه</h4>
        </div>
        <div class="mt-2">
          <!-- title input -->
          <div>
            <label for="exampleFormControlInput1" class="form-label">عنوان</label>
            <input v-model="expenseCategoryName" type="title" class="form-control p-2" id="title" />
          </div>

          <!-- the debt categoies -->
          <div class="mt-2">
            <label for="exampleFormControlInput1" class="form-label">سرفصل درآمد</label>
            <v-select :searchable="false" :clearable="false" v-model="selectedDebtCategory" dir="rtl" class="select-size-lg" :options="debtCategories" label="debt_category_name">
              <template #option="{ debt_category_name }">
                <span class="d-flex f16"> {{ debt_category_name }}</span>
              </template>
            </v-select>

            <!-- <v-select v-model="selectedDebtCategory" :options="debtCategories" :selectable="(option) => !option.value.includes('select_value')" label="text" /> -->
          </div>
          <div class="mt-3">
            <b-button block class="py-1" variant="primary" @click="store"> ثبت </b-button>
          </div>
        </div>
      </b-card>
    </main>
  </section>
</template>

<script>
import {
  BAvatar,
  BCard,
  BRow,
  BListGroup,
  BListGroupItem,
  BFormSelect,
  BButton,
  BModal,
  VBModal
} from "bootstrap-vue";
import ChartIcon from "@core/components/svg-icons/ChartIcon.vue";
import vSelect from "vue-select";
import { getExpenseAvailable, getCommonDebtCategory, expenseCategoryDelete, expenseCategoryStore } from "/src/api/costsServices";
import Ripple from 'vue-ripple-directive'
import ToastificationContent from "@core/components/toastification/ToastificationContent.vue"

export default {
  name: "ExpensesSeason",
  components: {
    ChartIcon,
    BAvatar,
    BRow,
    BCard,
    BListGroup,
    BListGroupItem,
    BFormSelect,
    BButton,
    vSelect,
    BModal,
    ToastificationContent
  },
  async mounted() {
    this.getInfo()
  },
  directives: {
    'b-modal': VBModal,
    Ripple,
  },
  data() {
    return {
      expenseCategoryName: null,
      selectedDebtCategory: null,
      debtCategories: [],
      getExpenses: [],
      expense: null,

      /**use for update expense */
      key: null
    };
  },
  methods: {
    /** get all page info  */
    async getInfo() {
      try {
        let { data } = await getExpenseAvailable();
        this.getExpenses = data.list;

        const cat = await getCommonDebtCategory()
        this.debtCategories = cat.data.list

      } catch (error) {
        console.error(error);
      }
    },

    // fill inputs
    fill(item, key) {
      this.expenseCategoryName = item
      this.key = key
    },

    /**store info */
    async store() {
      // validate 
      if (!this.expenseCategoryName) return this.$toast({
        component: ToastificationContent,
        props: {
          title: 'عنوان الزامی است',
          icon: 'AlertCircleIcon',
          variant: 'danger',
        },
      })

      if (!this.selectedDebtCategory) return this.$toast({
        component: ToastificationContent,
        props: {
          title: 'سرفصل درآمد الزامی است',
          icon: 'AlertCircleIcon',
          variant: 'danger',
        },
      })

      /**store the expense */
      try {
        await expenseCategoryStore({
          expense_category_id: this.key ? this.key : null,
          debt_category_id: this.selectedDebtCategory.debt_category_id,
          expense_category_name: this.expenseCategoryName
        })

        this.$toast({
          component: ToastificationContent,
          props: {
            title: 'سرفصل با موفقیت ذخیره شد',
            icon: 'CheckSquareIcon',
            variant: 'success',
          },
        })
        this.selectedDebtCategory = null
        this.expenseCategoryName = null
        this.key = null
        this.getInfo()
      } catch (error) {
        console.error(error)
      }
    },

    /**confirm remove and remove */
    async remove(key, item) {
      this.$bvModal
        .msgBoxConfirm(`حذف ${item} را تایید می کنید؟`, {
          title: 'هشدار',
          size: 'sm',
          okVariant: 'primary',
          okTitle: 'بله',
          cancelTitle: 'خیر',
          cancelVariant: 'outline-secondary',
          hideHeaderClose: true,
          centered: true,
        })
        .then(async (confirm) => {
          if (confirm) {
            try {
              await expenseCategoryDelete({ expense_category_id: key })
              this.$toast({
                component: ToastificationContent,
                props: {
                  title: 'حذف با موفقیت انجام شد',
                  icon: 'CheckCircle',
                  variant: 'success',
                },
              })
              this.getInfo()

            } catch (error) {
              console.error(error)
            }
          }
        })
    }
  }
};
</script>

